import React, {useEffect, useRef} from 'react';
import * as echarts from 'echarts';
import {px} from '../shared/px';
import {createEchartsOptions} from '../shared/create-echarts-options';

export const Chart1 = ()=>{
  const divRef = useRef(null);
  const myChart = useRef(null)
  const data = [
    {name: '兰州新区', value: 1},
    {name: '兰州新区', value: 2},
    {name: '兰州新区', value: 3},
    {name: '兰州新区', value: 4},
    {name: '兰州新区', value: 5},
    {name: '兰州新区', value: 6},
    {name: '兰州新区', value: 7},
    {name: '兰州新区', value: 8},
    {name: '兰州新区', value: 9},
  ]
  useEffect(()=> {
    setInterval(()=>{
      const newData = [
        {name: '兰州新区', value: Math.random() * 10},
        {name: '兰州新区', value: 2},
        {name: '兰州新区', value: 3},
        {name: '兰州新区', value: Math.random() * 10},
        {name: '兰州新区', value: 5},
        {name: '兰州新区', value: 6},
        {name: '兰州新区', value: Math.random() * 10},
        {name: '兰州新区', value: 8},
        {name: '兰州新区', value: 9},
      ]
      x(newData)
    },1000)
  },[])
  const x = (data:any[]) =>{
    // @ts-ignore
    myChart.current.setOption(createEchartsOptions({
      xAxis: {
        data: data.map(i => i.name),
        axisTick:{show: false},
        axisLine:{
          lineStyle:{
            color: '#083b70'
          }
        },
        axisLabel:{
          fontSize: px(12),
          formatter(val: string){
            if(val.length > 2){
              const array = val.split('')
              array.splice(2, 0, '\n')
              return array.join('')
            }else {
              return val
            }
          }
        }
      },
      yAxis: {
        splitLine:{
          show: false
        },
        axisLine: {
          show: true,
          lineStyle: {
            color: '#083b70'
          }
        },
        axisLabel: {
          fontSize: px(12)
        }
      },
      series: [{
        type: 'bar',
        data: data.map(i => i.value)
      }]
    }))
  }
  useEffect(()=>{
    // @ts-ignore
    myChart.current = echarts.init(divRef.current);
    x(data)
  },[])
  return(
    <div className="bordered 管辖统计">
      <h2>案发派出所管辖统计</h2>
      <div ref={divRef} className="chart"/>
    </div>
  )
}