import React, {useEffect, useRef} from 'react';
import * as echarts from 'echarts';
import {createEchartsOptions} from '../shared/create-echarts-options';
import {px} from '../shared/px';

export const Chart3 = ()=>{
  const divRef = useRef(null);
  const myChart = useRef(null)
  const data = [
    {name: '2010', rob: 0.01, drive: 0.02, steal: 0.03, kill: 0.04, hurt: 0.05},
    {name: '2011', rob: 0.02, drive: 0.03, steal: 0.04, kill: 0.05, hurt: 0.06},
    {name: '2012', rob: 0.03, drive: 0.04, steal: 0.05, kill: 0.06, hurt: 0.07},
    {name: '2013', rob: 0.04, drive: 0.05, steal: 0.06, kill: 0.07, hurt: 0.08},
    {name: '2014', rob: 0.05, drive: 0.06, steal: 0.07, kill: 0.08, hurt: 0.09},
    {name: '2015', rob: 0.06, drive: 0.07, steal: 0.08, kill: 0.09, hurt: 0.1},
    {name: '2016', rob: 0.07, drive: 0.08, steal: 0.09, kill: 0.1, hurt: 0.11},
    {name: '2017', rob: 0.08, drive: 0.09, steal: 0.1, kill: 0.11, hurt: 0.12},
    {name: '2018', rob: 0.09, drive: 0.1, steal: 0.11, kill: 0.12, hurt: 0.13},
  ]
  useEffect(()=>{
    setInterval(()=>{
      const newData = [
        {name: '2010', rob: Math.random() / 10, drive: 0.02, steal: 0.03, kill: Math.random() / 10, hurt: Math.random() / 10},
        {name: '2011', rob: 0.02, drive: Math.random() / 10, steal: Math.random() / 10, kill: 0.05, hurt: 0.06},
        {name: '2012', rob: Math.random()  / 10, drive: 0.04, steal: 0.05, kill: Math.random() / 10, hurt: Math.random() / 10},
        {name: '2013', rob: 0.04, drive: Math.random()  / 10, steal: Math.random() / 10, kill: 0.07, hurt: Math.random() / 10},
        {name: '2014', rob: 0.05, drive: 0.06, steal: 0.07, kill: 0.08, hurt: 0.09},
        {name: '2015', rob: Math.random()  / 10, drive: Math.random() / 10, steal: 0.08, kill:Math.random() / 10, hurt: 0.1},
        {name: '2016', rob: 0.07, drive: Math.random() / 10, steal: Math.random() / 10, kill:Math.random() / 10, hurt: Math.random() / 10},
        {name: '2017', rob: Math.random()  / 10, drive: 0.09, steal: Math.random() / 10, kill: 0.11, hurt: 0.12},
        {name: '2018', rob: 0.09, drive: Math.random() / 10, steal: 0.11, kill: Math.random() / 10, hurt: Math.random() / 10},
      ]
      x(newData)
    },1000)
  },[])
  const x = (data: any[])=>{
    // @ts-ignore
    myChart.current.setOption(createEchartsOptions({
      legend: {
        bottom:px(10),
        textStyle:{
          color: 'white',
        },
        itemHeight:px(16),
        itemWidth:px(30)
      },
      grid:{
        left: px(20),
        top: px(20),
        right: px(20),
        bottom: px(70),
        containLabel: true
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: data.map(i => i.name),
        splitLine:{
          show: true,
          lineStyle:{
            color: '#073e78'
          }
        },
        axisTick:{
          show: false
        },
        axisLine:{
          show: false
        }
      },
      yAxis: {
        type: 'value',
        splitLine: {
          lineStyle: {
            color: '#073e78'
          }
        },
        axisLabel:{
          formatter(val: number){
            return val * 100 + '%'
          }
        }
      },
      series: [
        {
          name: '抢劫',
          type: 'line',
          data: data.map(i=>i.rob).reverse()
        },
        {
          name: '醉驾',
          type: 'line',
          data: data.map(i=>i.drive).reverse()
        },
        {
          name: '盗窃',
          type: 'line',
          data: data.map(i=>i.steal).reverse()
        },
        {
          name: '故意杀人',
          type: 'line',
          data: data.map(i=>i.kill).reverse()
        },
        {
          name: '故意伤人',
          type: 'line',
          data: data.map(i=>i.hurt).reverse()
        }
      ].map(obj => ({
        ...obj,
        symbol: 'circle',
        symbolSize: px(12),
        lineStyle: {
          width: px(2)
        }
      }))
    }))
  }
  useEffect(()=>{
    // @ts-ignore
    myChart.current = echarts.init(divRef.current);
    x(data)
  },[])
  return(
    <div className="bordered 发案趋势">
      <h2>法案趋势分析</h2>
      <div ref={divRef} className="chart"/>
    </div>
  )
}