import React, {useEffect, useRef} from 'react';
import * as echarts from 'echarts';
import {createEchartsOptions} from '../shared/create-echarts-options';
import {px} from '../shared/px';

export const Chart4 = ()=>{
  const divRef = useRef(null);
  const myChart = useRef(null)
  const data = [
    {name: 0, value: 0.15},
    {name: 2, value: 0.13},
    {name: 4, value: 0.11},
    {name: 6, value: 0.13},
    {name: 8, value: 0.14},
    {name: 10, value: 0.15},
    {name: 12, value: 0.16},
    {name: 14, value: 0.18},
    {name: 16, value: 0.21},
    {name: 18, value: 0.19},
    {name: 20, value: 0.17},
    {name: 22, value: 0.16},
    {name: 23, value: 0.15},
  ]
  useEffect(()=>{
    setInterval(()=>{
      const newData = [
        {name: 0, value: Math.random() / 10},
        {name: 2, value: 0.13},
        {name: 4, value: Math.random() / 10},
        {name: 6, value: 0.13},
        {name: 8, value: 0.14},
        {name: 10, value: 0.16},
        {name: 12, value: 0.16},
        {name: 14, value: 0.18},
        {name: 16, value: 0.21},
        {name: 18, value: 0.19},
        {name: 20, value: 0.17},
        {name: 22, value: Math.random() / 10},
        {name: 23, value: Math.random() / 10},
      ]
      x(newData)
    },1000)
  },[])
  const x = (data: any[])=>{
    // @ts-ignore
    myChart.current.setOption(createEchartsOptions({
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: data.map(i=>i.name),
        splitLine:{
          show:true,
          lineStyle:{
            color: '#073e78'
          }
        },
        axisTick:{
          show: false
        },
        axisLine:{
          show:false
        }
      },
      yAxis: {
        type: 'value',
        splitLine:{
          show:true,
          lineStyle:{
            color: '#073e78'
          }
        },
        axisLabel:{
          formatter(val: number){
            return val * 100 + '%'
          }
        }
      },
      series: [{
        type: 'line',
        data: data.map(i=>i.value),
        symbol: 'circle',
        symbolSize: px(12),
        lineStyle:{
          width: px(2)
        },
        areaStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 1,
            color: '#414a9f'
          },{
            offset: 1,
            color: '#1b1d52'
          }])
        }
      }]
    }))
  }
  useEffect(()=>{
    // @ts-ignore
    myChart.current = echarts.init(divRef.current);
    x(data)
  },[])
  return(
    <div className="bordered 案发时段">
      <h2>案发时段分析</h2>
      <div ref={divRef} className="chart"/>
    </div>
  )
}